.App {
  font-family: sans-serif;
  text-align: center;
}
body {
  background-color: wheat;
}

h1 {
  color: blue;
  font-weight: bolder;
  border-style: solid;
  border-color: green;
  margin-bottom: 2rem;
}
p {
  color: blue;
  font-weight: bolder;
  font-size: 1.5rem;
}
button {
  padding: 12px;
  margin: 10px;
  font-weight: bold;
  background-color: yellow;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  color: orangered;
}
hr {
  width: 100%;
}

li {
  text-align: center;
  border: 2px solid green;
  font-size: 1rem;

  padding: 0.5rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}

ul {
  padding: 1rem;
  list-style: none;
  margin: auto;
  width: 60%;
}
li div {
  padding: 3px;
}

.name {
  color: blue;
  font-weight: bold;
}

.count {
  color: orangered;
  font-weight: bold;
}
